
import Vue from 'vue'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Tag from 'c%/Tag.vue'
export default{
mixins: [common,globals],
components: {
Card,
Tag,
Flexbox,
FlexboxItem
},
data() {
return {
data: []
}
},
watch: {
data(data) {
this.data.forEach(item=>{
Vue.set(item,'$$expand',false)
})
}
},
methods: {
getExamtype(item) {
return this.$filter('dict_title')(item.student_exam.exam_type_did,'exam_type')
},
expandContent(item) {
item.$$expand = !item.$$expand
},
getAvgScore(item) {
return (util.div(item.total_score,item.student_exam.exam_subject_dids.length)).toFixed(2)
}
},
computed: {
data_uri() {
return 'student_exam_scores?sid='+this.sid$+'&with=one_class,student_exam,student_exam_subject_score'
}
}
}



